const { Option } = Select
const isVideo = str => {
    return /^.+(\.mp4|\.avi|video|\.mov|\.rmvb|.flv)$/i.test(str)
}

const insColumns = [
    {
        title: 'Item #',
        dataIndex: 'item',
        key: 'item',
        align: 'center'
    },
    {
        title: 'Date',
        dataIndex: 'date',
        key: 'date',
        align: 'center'
    },
    {
        title: '#of Impress',
        dataIndex: 'impress',
        key: 'impress',
        align: 'center'
    },
    {
        title: '# of likes',
        dataIndex: 'likes',
        key: 'likes',
        align: 'center'
    },
    {
        title: '# of comments',
        dataIndex: 'comments',
        key: 'comments',
        align: 'center'
    }
]

const columns = state => [
    {
        title: 'Task id',
        dataIndex: 'task_id',
        key: 'task_id',
        align: 'center'
    },
    {
        title: 'User',
        dataIndex: 'user',
        key: 'user',
        align: 'center',
        render: (text, item) => {
            return <div>{state.formData.user_name}</div>
        }
    },
    {
        title: 'Review time',
        dataIndex: 'review_time',
        key: 'review_time',
        align: 'center',
        render: (text, item) => {
            return <div>{text ? new Date(text).toLocaleString() : '--'}</div>
        }
    },
    {
        title: 'Review images',
        dataIndex: 'images',
        key: 'images',
        align: 'center',
        render: (text, item) => (
            <div style={{ display: 'flex' }}>
                {text &&
                    JSON.parse(text).map((url, index) => {
                        console.log('urlll,', url)
                        if (isVideo(url)) {
                            return (
                                <video src={url} controls='controls' style={{ width: '100px', marginLeft: '10px' }}>
                                    您的浏览器不支持 video 标签。
                                </video>
                            )
                        } else {
                            return <Image src={url} style={{ maxWidth: '70px', marginLeft: '10px' }} />
                        }
                    })}
            </div>
        )
    },
    {
        title: 'Review linkes',
        dataIndex: 'linkes',
        key: 'linkes',
        align: 'center',
        render: (text, item) => {
            return isJSON(text)
                ? JSON.parse(text).map((iitem, index) => {
                      return (
                          <div key={index}>
                              <a href={iitem} key={index} target='_blank'>
                                  {iitem}
                              </a>
                          </div>
                      )
                  })
                : text
        }
    },
    {
        title: 'Reviewed by',
        dataIndex: 'operator_name',
        key: 'operator_name',
        align: 'center'
    },
    {
        title: 'Reviewed decision',
        dataIndex: 'status',
        key: 'status',
        align: 'center',
        render: (text, item) => (text == 0 ? 'Reject' : text == 1 ? 'Pending for review' : text == 2 ? 'approved' : '')
    },
    {
        title: 'Reason for not-approved',
        dataIndex: 'id',
        key: 'id',
        align: 'center',
        render: (text, item) =>
            item.review_reason == null ? '' : item.review_reason == 'Approved' ? 'Approved' : item.review_reason
    }
]

