const TaskDetail = props => {
    const [state, setState] = useState({
        reviewContent: {},
        taskRecordList: [],
        currentExamine: {},
        visible: false,
        formData: {},
        user: '',
        userID: '',
        taskTreeData: []
    })
    const [postState, setPostState] = useState({
        postTime: '',
        postUrl: '',
        postType: '',
        postList: []
    })

    const [form] = Form.useForm()
    const [country, setCountry] = useState([{ name: 'china', value: '1' }])

    const getCountryList = () => {
        APIGetCountryList().then(resp => {
            setCountry(resp.data.data)
        })
    }

    const getPostDetail = () => {
        APIShareMediaDetail({ taskId: props.match.params.id }).then(resp => {
            setPostState({
                ...postState,
                postTime: resp.data.data.postTime ? resp.data.data.postTime : '--',
                postUrl: resp.data.data.postUrl ? resp.data.data.postUrl : '--',
                postType: resp.data.data.type ? resp.data.data.type : '--',
                postList: resp.data.data.record ? resp.data.data.record : []
            })
        })
    }

    const initForm = () => {
        let query =
            props.location.query || (sessionStorage.getItem('query') ? JSON.parse(sessionStorage.getItem('query')) : {})
        let id = props.match.params.id
        APIGetTaskDetailNew({ id: id }).then(resp => {
            console.log('resp', resp)
            let taskRecordList = resp.data.result.recordNewRsps
            let currentExamine = taskRecordList && taskRecordList.length ? taskRecordList[0] : {}
            setState({
                ...state,
                formData: resp.data.result,
                taskRecordList: taskRecordList,
                currentExamine: currentExamine
            })
        })
    }

    const onCreate = values => {
        console.log('valuessss', values)
        APITaskRejectNew({
            id: values.id,
            review_reason: values.review_reason
        }).then(resp => {
            message.success('success')
            Modal.confirm({
                title: 'Back to previous page',
                cancelText: 'Stay on this page',
                onCancel: () => {},
                onOk: () => {
                    console.log('ok')
                    props.history.push('/tasks_ma/tasks_new')
                }
            })
        })
    }

    const agreeExamClick = id => {
        APITaskApprovedNew({ id: id }).then(resp => {
            message.success('success')
            Modal.confirm({
                title: 'Back to previous page',
                cancelText: 'Stay on this page',
                onCancel: () => {},
                onOk: () => {
                    console.log('ok')
                    props.history.push('/tasks_ma/tasks_new')
                }
            })
        })
    }

    const rejectExamClick = id => {
        setState({
            ...state,
            visible: true,
            formData: {
                id: id
            }
        })
    }

    useEffect(() => {
        getCountryList()
        initForm()
        getPostDetail()
    }, [])

    return (
        <Layout className='index animated fadeIn'>
            <DetailModal
                visible={state.visible}
                onCreate={onCreate}
                onCancel={() => {
                    setState({ ...state, visible: false })
                }}
                formData={state.formData}
                disabled={state.disabled}
            />
            <WebBreadcrumbNew title='task Detail' />
            <div className='base-style base-detail' id='demoline'>
                <Row>
                    <Col span={24}>
                        <Descriptions title='Task Info'>
                            <Descriptions.Item label='Task ID'>{state.formData.id}</Descriptions.Item>
                            <Descriptions.Item label='Campaign ID'>
                                <a href={webhost + '/createCampaignNew/' + state.formData.business_id} target='_blank'>
                                    {state.formData.business_id}
                                </a>
                            </Descriptions.Item>
                            <Descriptions.Item label='Submitted form'>
                                <a href={webhost + '/userDetail/' + state.formData.user_id} target='_blank'>
                                    {state.formData.user_name}
                                </a>
                            </Descriptions.Item>
                            <Descriptions.Item label='Campaign Title' span={3}>
                                {state.formData.title}
                            </Descriptions.Item>
                            <Descriptions.Item label='Submitted time'>
                                {state.formData.submitted_time ? state.formData.submitted_time : '--'}
                            </Descriptions.Item>
                            <Descriptions.Item label='Attempt'>{state.taskRecordList.length}</Descriptions.Item>
                            <Descriptions.Item label='Code applied'>{state.formData.applied_code}</Descriptions.Item>
                            <Descriptions.Item label='Task status' span={3}>
                                <Badge status={state.formData.status == 0 ? 'default' : 'success'} />
                                {state.formData.status == 0
                                    ? 'Task closed'
                                    : state.formData.status == 1
                                    ? 'New Task'
                                    : state.formData.status == 2
                                    ? 'Pending for submit'
                                    : state.formData.status == 3
                                    ? 'Pending for Review'
                                    : state.formData.status == 4
                                    ? 'Review Rejected'
                                    : state.formData.status == 5
                                    ? 'Sub Process Review Approved'
                                    : state.formData.status == 6
                                    ? 'Whole Process Review Approved'
                                    : ''}
                            </Descriptions.Item>
                            <Descriptions.Item label='Sample application ID' span={3}>
                                {state.formData.sampleOrderId ? state.formData.sampleOrderId : '--'}
                            </Descriptions.Item>
                            <Descriptions.Item label='Sample Status' span={3}>
                                {state.formData.sampleOrderStatusName ? state.formData.sampleOrderStatusName : '--'}
                            </Descriptions.Item>
                        </Descriptions>
                    </Col>
                </Row>
            </div>

            {state.currentExamine.status == 1 ? (
                <div className='base-style base-detail'>
                    <h3 style={{ marginBottom: '10px' }}>Pending for Review</h3>
                    <div className='task-review-content'>
                        <h4>Upload imgs:</h4>
                        <div style={{ marginLeft: '20px' }}>
                            <div className='imgvideo'>
                                {state.currentExamine.images &&
                                    JSON.parse(state.currentExamine.images).map((item, index) => {
                                        return isVideo(item) ? (
                                            <video
                                                src={item}
                                                className='marginleft5'
                                                controls='controls'
                                                style={{ width: '150px', marginLeft: '4px' }}></video>
                                        ) : (
                                            <Image className='marginleft5' key={index} width={150} src={item} />
                                        )
                                    })}
                            </div>
                        </div>
                    </div>
                    <Divider />
                    <div className='task-review-content'>
                        <h4>Links:</h4>
                        <div style={{ marginLeft: '20px' }}>
                            {state.currentExamine.linkes ? (
                                <div>
                                    {JSON.parse(state.currentExamine.linkes).map((item, index) => {
                                        return (
                                            <a key={index} href={item} target='_blank'>
                                                {item}
                                            </a>
                                        )
                                    })}
                                </div>
                            ) : null}
                        </div>
                    </div>
                    <Divider />
                    <div className='task-review-content'>
                        <h4>Content:</h4>
                        <div style={{ marginLeft: '20px' }}>
                            <span>{state.currentExamine.content}</span>
                        </div>
                    </div>

                    <div style={{ marginTop: '20px', textAlign: 'center' }}>
                        <Button
                            type='primary'
                            onClick={() => {
                                agreeExamClick(state.currentExamine.id)
                            }}>
                            Approve
                        </Button>
                        <Button
                            type='dashed'
                            onClick={() => {
                                rejectExamClick(state.currentExamine.id) //8
                            }}
                            style={{ marginLeft: '20px' }}>
                            Reject
                        </Button>
                    </div>
                </div>
            ) : null}

            <div className='base-style base-detail'>
                <Row>
                    <Col span={24}>
                        <Descriptions title='Performance for instagram' layout='vertical'>
                            <Descriptions.Item label='Post time'> {postState.postTime} </Descriptions.Item>
                            <Descriptions.Item label='Post URL'>
                                <a href={webhost + '/createCampaign/' + state.formData.campaign_id} target='_blank'>
                                    {postState.postUrl}
                                </a>
                            </Descriptions.Item>
                            <Descriptions.Item label='Type'>
                                <a href={webhost + '/userDetail/' + state.formData.user_id} target='_blank'>
                                    {postState.postType}
                                </a>
                            </Descriptions.Item>
                        </Descriptions>
                        <Table className='mt-50' columns={insColumns} dataSource={postState.postList} bordered />
                    </Col>
                </Row>
            </div>

            <div className='base-style base-detail' id='demoline'>
                <Row>
                    <Col span={24}>
                        <h3>Reviewd logs</h3>
                        <Table columns={columns(state)} dataSource={state.taskRecordList} bordered />
                    </Col>
                </Row>
            </div>
        </Layout>
    )
}

